import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
    selector: 'findqo-sort-menu',
    templateUrl: './sort-menu.component.html',
    styleUrl: './sort-menu.component.scss',
    standalone: false
})
export class SortMenuComponent {
	@Input() columnName: string;
	@Input() activeColumn: { colName?: string; direction?: string };
	@Input() ascText: string;
	@Input() descText: string;
	@Input() direction: string | null;
	@Output() sortChange = new EventEmitter<{ column: string; direction: string }>();

	onSort(direction: string) {
		this.sortChange.emit({ column: this.columnName, direction });
	}
}
